<template>
  <div id="userWallet">
    <div class="content">
      <div class="walletBox">
        <div class="walletCard">
          <p class="title">我的余额（元）</p>
          <div class="middle">
            <p class="balance">{{walletData.balance}}</p>
            <van-button round class="withdrawal" @click="withdrawal">立即提现</van-button>
          </div>
          <div class="bottom">
            <div class="yesterdayIncome">
              <p class="numericalInfo">昨日收益（元）</p>
              <p class="numerical">{{walletData.yes_amounts}}</p>
            </div>
            <div class="todayIncome">
              <p class="numericalInfo">今日收益（元）</p>
              <p class="numerical">{{walletData.today_amounts}}</p>
            </div>
            <div class="totalWithdrawal">
              <p class="numericalInfo">累计提现（元）</p>
              <p class="numerical">{{walletData.withdraw_amounts}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="navBox">
        <nav-block :title="'收支明细'" :to="'/accountDetail'" class="toAccountDetail"></nav-block>
      </div>
    </div>
    <van-overlay :show="load" :z-index="10000" class="loadBox" :lock-scroll="true">
      <van-loading class="load" type="spinner" color="#1989fa" :vertical="true" v-if="load" />
    </van-overlay>
  </div>
</template>
<script>
import navBlock from "../components/navBlock.vue";
import { getWalletData } from "../assets/javascript/apis.js";
export default {
  components: {
    navBlock,
  },
  data() {
    return {
      walletData: {},
      load: true,
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.load = true;
      getWalletData().then((res) => {
        console.log(res);
        this.walletData = res;
        this.load = false;
      });
    },
    //提现的相关逻辑
    withdrawal() {
      if (Number(this.walletData.balance) > 0) {
        this.$router.push("/withDrawal");
      } else {
        this.$toast({
          position: "top",
          message: "抱歉，您的账户内暂无可提现余额",
        });
      }
    },
  },
};
</script>
<style lang="scss" scoped>
#userWallet {
  width: 100%;
  height: 100%;
  .loadBox {
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .content {
    width: 100%;
    .walletBox {
      padding: 20px;
      .walletCard {
        width: 100%;
        height: 175px;
        padding: 10px 15px;
        box-sizing: border-box;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 6px 16px 0px rgba(0, 0, 0, 0.08);
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        .title {
          font-size: 14px;
          color: rgba(85, 85, 85, 1);
        }
        .middle {
          display: flex;
          justify-content: space-between;
          .balance {
            font-size: 30px;
            font-family: DIN;
            font-weight: 500;
            color: rgba(7, 7, 7, 1);
          }
          .withdrawal {
            width: 85px;
            height: 25px;
            padding: 0;
            background: linear-gradient(
              0deg,
              rgba(76, 170, 241, 1),
              rgba(155, 120, 240, 1)
            );
            border-radius: 12px;
            font-size: 14px;
            color: rgba(255, 255, 255, 1);
          }
        }
        .bottom {
          display: flex;
          justify-content: space-between;
          > div {
            display: flex;
            flex-direction: column;
            &.todayIncome {
              padding: 0 15px;
              box-sizing: border-box;
              border-left: 1px solid rgba(204, 204, 204, 1);
              border-right: 1px solid rgba(204, 204, 204, 1);
            }
          }
          .numericalInfo {
            margin-bottom: 10px;
            font-size: 12px;
            color: rgba(170, 170, 170, 1);
          }
          .numerical {
            font-size: 15px;
            font-family: DIN;
            font-weight: 500;
            color: rgba(7, 7, 7, 1);
          }
        }
      }
    }
    .navBox {
      padding: 0 0 0 20px;
      .toAccountDetail {
        margin-top: 15px;
        font-size: 14px;
        color: rgba(85, 85, 85, 1);
      }
    }
  }
}
</style>